<template>
    <div>

         <el-row :gutter="10">
          <!-- 搜索添加 -->
           <el-col :span="10">
             <el-input placeholder="请输入查询的热词" v-model="KW" clearable  >

             </el-input>
          </el-col>
          <el-col :span="4">
             
                <el-select v-model="year" clearable placeholder="日期">
                     <el-option
                          v-for="item in yearoptions"
                      :key="item.value"
                    :label="item.label"
                   :value="item.label">
                  </el-option>
                   </el-select>
          </el-col>
          <el-col :span="4">
             
                 <el-select v-model="source" clearable placeholder="来源">
                     <el-option
                          v-for="item in typeoptions"
                      :key="item.value"
                    :label="item.label"
                   :value="item.label">
                  </el-option>
                   </el-select>
          </el-col>
          
        <el-col :span="1">
            <el-button plain @click="getlianjieInfo">搜索</el-button>

        </el-col>
       </el-row>
<el-button  type="success" icon='el-icon-plus' plain style="float: right; margin-bottom:10px" @click='addDialogVisible=true'>添加链接</el-button>    
    <el-table  :data= "lianjie_list"  stripe  style="font-size: 15px " >  
          <el-table-column label='题目' prop='title' width="500"  ></el-table-column>
          <el-table-column label='时间' prop='timestamp' width="150" sortable ></el-table-column>
          <el-table-column label='来源' prop='source' width="150"  ></el-table-column>
          <el-table-column label='链接'  width="300"  >
             <template slot-scope="props">
                <el-link :href="props.row.url" target="_blank">{{props.row.url}}</el-link>
                </template>
            </el-table-column>
             <el-table-column label='操作'  width="100"  >
             <template slot-scope="props">
                <el-link @click="shanchu(props.row.title)"  target="_blank">删除</el-link>
                </template>
            </el-table-column>
    </el-table>
   <div>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
      :current-page="pageNum" :page-sizes="[1, 2, 5, 10]" :page-size="pageSize"
      layout="sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </div>
    
    <el-dialog title='添加链接' :visible.sync='addDialogVisible' width="50%" @close='resetlianjieForm'>
     
      <el-form  ref="lianjieFormRef"  :model="lianjie_info" label-width="200px" >
        <el-form-item label="原网页标题" prop='title'>
          <el-input  v-model="lianjie_info.title"  style="width:500px;"></el-input>
        </el-form-item>
      <el-form-item label="时间" prop='timestamp'>
          <el-input  v-model="lianjie_info.timestamp"  style="width:500px;"></el-input>
        </el-form-item>
        <el-form-item label="来源" prop='source'>
          <el-input  v-model="lianjie_info.source"  style="width:500px;"></el-input>
        </el-form-item>
        <el-form-item label="链接" prop='lianjie'>
          <el-input  v-model="lianjie_info.lianjie"  style="width:500px;"></el-input>
        </el-form-item>
        <el-form-item >
           <el-button   @click="resetlianjieForm" plain>重置</el-button>
           <el-button type="primary" plain @click="addlianjie">确定</el-button>
        </el-form-item>      
      </el-form>
    </el-dialog>
    </div>
</template>
<script>
export default{
    created(){
      if(this.$route.query.KW !=null){
          this.KW=this.$route.query.KW;
          console.log(this.KW);
      }else{
        this.KW="";
      }
           this.getlianjieInfo();
    },
    data(){
     return{
            addDialogVisible:false,
            lianjie_list:[],
            pageNum:1,
            pageSize:10,
            total:0,
          
         KW:"",
        year:"",
        yearoptions: [{
          value: '选项1',
          label: '2021'
        }, {
          value: '选项2',
          label: '2020'
        }, {
          value: '选项3',
          label: '2019'
        }, {
          value: '选项4',
          label: '2018'
        }, {
          value: '选项5',
          label: '2017'
        }],

        source:"",
        typeoptions: [{
          value: '选项1',
          label: '央视网'
        }, {
          value: '选项2',
          label: '网易新闻'
        }, {
          value: '选项3',
          label: '央广网'
        }],
       lianjie_info:{
           title:"",
           timestamp:"",
           source:"",
           lianjie:"",
         },
     };
    },

    methods:{
         

         
         async getlianjieInfo(){
             const order_query={};
             order_query.KW=this.KW;
             order_query.year=this.year;
             order_query.source=this.source;
             order_query.pageNum=this.pageNum-1;
             order_query.pageSize=this.pageSize;
             
            console.log(order_query);
          
           const {data:res}= await this.$http.post("lianjie_query",order_query);
           console.log(res.lianjie_list);
           this.total=res.num;
           this.lianjie_list=res.lianjie_list;
         },
         resetlianjieForm(){
            this.$refs.lianjieFormRef.resetFields();
      },
       async addlianjie(){
             
          console.log(this.lianjie_info);
           const {data:res}= await this.$http.post("addlianjie",this.lianjie_info);
          if(res=="false")
               { 
                      this.$message.warning("添加失败，请重试！");
                this.getlianjieInfo();
              }else if(res=="ok")
               {
                 this.$message.success("添加成功！！！");
                 this.getlianjieInfo();
              }
         },
        handleSizeChange(newSize)
         {
             this.pageSize=newSize;
             this.getlianjieInfo();
         },

         handleCurrentChange(newPage)
         {
             this.pageNum=newPage;
             this.getlianjieInfo();
         },
         
          async shanchu(title){
      this.$confirm('确定要删除吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
            
            const order_query={};
             order_query.title=title;
             const {data:res}= await this.$http.post("shanchulianjie",order_query);

             if(res=="false")
               { 
                      this.$message.warning("删除失败，请重试！");
                   this.getlianjieInfo();
              }else if(res=="ok")
               {
                 this.$message.success("删除成功！！！");
                 this.getlianjieInfo();
              }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }, 
    },
   
}
</script>
<style lang="less" scoped>
 .demo-table-expand {
        font-size: 20;
    }
.demo-table-expand label {
        width: 150px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 20;
        width: 33%;
        font-size:40;
        font-family: "微软雅黑", Courier, monospace;
        font-weight: 500;
    }
 
</style>