<template>
  <div >
     
    
    <el-row :gutter="10">
          <!-- 搜索添加 -->
           <el-col :span="10">
             <el-input placeholder="添加链接" v-model="url" clearable  >

             </el-input>
          </el-col>
          
        <el-col :span="1">
            <el-button plain @click="tianjiaurl">添加链接</el-button>
        </el-col>
       </el-row>
   <el-table  :data= "url_list"  stripe  style="font-size: 15px " >  
         
          <el-table-column label='链接'  width="300"  >
             <template slot-scope="props">
                <el-link :href="props.row" target="_blank">{{props.row}}</el-link>
                </template>
            </el-table-column>
            <el-table-column label='操作'  width="300"  >
             <template slot-scope="props">
                <el-link @click="shanchu(props.row)"  target="_blank">删除</el-link>
                </template>
            </el-table-column>
    </el-table>
    
  


  </div>
</template>

<script>
export default {
   created(){
      
           this.geturl();
    },
  data() {
    return{
      url_list:[],
      url:"",
    }
  },

  methods:{
   
    async geturl(){
            const {data:res}= await this.$http.post("geturl");
            this.url_list=res.url_list;
            console.log(this.url_list);
           
       },

    async shanchu(url){
      this.$confirm('确定要删除吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
            
            const order_query={};
             order_query.url=url;
             const {data:res}= await this.$http.post("shanchuurl",order_query);

             if(res=="false")
               { 
                      this.$message.warning("删除失败，请重试！");
                this.geturl();
              }else if(res=="ok")
               {
                 this.$message.success("删除成功！！！");
                 this.geturl();
              }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }, 
    async tianjiaurl(){
      const order_query={};
             order_query.url=this.url;
             const {data:res}= await this.$http.post("tianjiaurl",order_query);
             console.log(res);
             if(res=="false")
               { 
                      this.$message.warning("添加失败，请重试！");
                this.geturl();
              }else if(res=="ok")
               {
                 this.$message.success("添加成功！！！");
                 this.geturl();
              }
    } 

    
   
  },
};
</script>

<style lang="less" scoped>

// 跟节点样式
.login_container {
  background-color: #fff;
  height: 100%;
}
// 登录框
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;// 圆角
  position: absolute;// 绝对定位
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);// 根据自己位置 以自己为长度位移
  border: 1px solid #fff;
 // 头像框
  .avatar_box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%; // 加圆角
    padding: 10px;
    box-shadow: 0 0 10px #ddd;// 盒子阴影
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #0ee;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%; // 加圆角
      background-color: #eee;
    }
  }
  .btns {
    display: flex;// 弹性布局
    justify-content: flex-end; // 尾部对齐
  }
  .login_form {
    position: absolute;
    bottom: 0%;
    width: 100%;
    padding: 0 10px;
    box-sizing: border-box;// 设置边框
  }
}

.el-carousel__item h3 {
      color: #475669;
      font-size: 14px;
      opacity: 0.75;
      line-height: 200px;
      margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
      background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
      background-color: #d3dce6;
    }
    img{
     /*设置图片宽度和浏览器宽度一致*/
      width:100%;
      height:inherit;
    }
</style>