<template>
    <div>

         

    <el-table  :data= "user_list"  stripe  style="font-size: 15px " >  
          <el-table-column label='用户名' prop='user_id' width="250"  ></el-table-column>
          <el-table-column label='类型' prop='type' width="250" ></el-table-column>
          <el-table-column label='电话' prop='phone' width="250"  ></el-table-column>
         
             <el-table-column label='操作'  width="100"  >
             <template slot-scope="props">
                <el-link @click="shanchu(props.row.user_id)"  target="_blank">删除用户</el-link>
                </template>
            </el-table-column>
    </el-table>
   
    
   
    </div>
</template>
<script>
export default{
    created(){
     
           this.getuser();
    },
    data(){
     return{
            user_list:[],
     };
    },

    methods:{
         

         
         async getuser(){
           const {data:res}= await this.$http.post("getuser");
           this.user_list=res.user_list;
         },
        
         
          async shanchu(user_id){
      this.$confirm('确定要删除吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
            
            const order_query={};
             order_query.user_id=user_id;
             const {data:res}= await this.$http.post("shanchuuser",order_query);

             if(res=="false")
               { 
                      this.$message.warning("删除失败，请重试！");
                this.getuser();
              }else if(res=="ok")
               {
                 this.$message.success("删除成功！！！");
                 this.getuser();
              }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
    }, 
    },
   
}
</script>
<style lang="less" scoped>
 .demo-table-expand {
        font-size: 20;
    }
.demo-table-expand label {
        width: 150px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 20;
        width: 33%;
        font-size:40;
        font-family: "微软雅黑", Courier, monospace;
        font-weight: 500;
    }
 
</style>