<template>
    <div>
         <h1 align="center"  style="font-size: 25px ">欢迎使用山东大学舆情分析系统</h1>
        
        <el-carousel :interval="2000" type="card" height="330px" style="margin-top:50px;">
            <el-carousel-item v-for="item in imagebox" :key="item.id">
             <img :src="item.idView" class="image">
          </el-carousel-item>
    </el-carousel>

    </div>
</template>

<script>
export default {
    data(){
      return {
        imagebox:[{id:0,idView:require('../assets/image/2.jpg')},
        {id:1,idView:require('../assets/image/3.jpg')},
        {id:2,idView:require('../assets/image/1.jpg')}
        ],
         
        // 浏览器宽度
        screenWidth :0
      };
    },
      methods:{
              setSize:function () {
                // 通过浏览器宽度(图片宽度)计算高度
                this.bannerHeight = 400 / 1920 * this.screenWidth;
               },
              },
      mounted() {
              // 首次加载时,需要调用一次
              this.screenWidth =  window.innerWidth;
              this.setSize();
              // 窗口大小发生改变时,调用一次
              window.onresize = () =>{
              this.screenWidth =  window.innerWidth;
              this.setSize();
            }
          },
}
</script>

<style lang="less" scoped>
.el-carousel__item h3 {
      color: #475669;
      font-size: 14px;
      opacity: 0.75;
      line-height: 200px;
      margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
      background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
      background-color: #d3dce6;
    }
    img{
     /*设置图片宽度和浏览器宽度一致*/
      width:100%;
      height:inherit;
    }
</style>