<template>
    <div>

         <el-row >
          <!-- 搜索添加 -->
          <el-col :span="10">
             <el-input placeholder="请输入查询的热词" v-model="infoKW" clearable  >
                 <el-button slot="append" icon="el-icon-search" @click="getKWInfo"></el-button>
             </el-input>
          </el-col>
       </el-row>

<el-button  type="success" icon='el-icon-plus' plain style="float: right; margin-bottom:10px" @click='addDialogVisible=true'>添加热词</el-button>    
       <el-table  :data= "kw_list"  stripe  style="font-size: 15px " >  
          <el-table-column label='热词' prop='kW' width="100"  ></el-table-column>
          <el-table-column label='出现次数' prop='times' width="120"  sortable></el-table-column>
          <el-table-column label='来源' prop='sources' width="200"  ></el-table-column>
          <el-table-column label='相关热词' prop='otherKW' width="600"  ></el-table-column>
        
             <el-table-column label='热词信息'  width="125">
                 <template slot-scope="props">
                <el-link  @click="juti(props.row.kW)">热词信息</el-link>
                </template>
            </el-table-column>
             <el-table-column label='操作'  width="100">
                 <template slot-scope="props">
                <el-link  @click="shanchu(props.row.kW)">删除</el-link>
                </template>
            </el-table-column>
    </el-table>
   <div>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
      :current-page="pageNum" :page-sizes="[1, 2, 5, 10]" :page-size="pageSize"
      layout="sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </div>
 
 <el-dialog title='添加热词' :visible.sync='addDialogVisible' width="50%" @close='resetreciForm'>
     
      <el-form  ref="reciFormRef"  :model="reci_info" label-width="200px" >
        <el-form-item label="热词" prop='KW'>
          <el-input  v-model="reci_info.KW"  style="width:500px;"></el-input>
        </el-form-item>
      <el-form-item label="总次数" prop='times'>
          <el-input  v-model="reci_info.times"  style="width:500px;"></el-input>
        </el-form-item>
        <el-form-item label="每天出现次数" prop='days'>
          <el-input  v-model="reci_info.days"  style="width:500px;"></el-input>
        </el-form-item>
        <el-form-item label="每月出现次数" prop='months'>
          <el-input  v-model="reci_info.months"  style="width:500px;"></el-input>
        </el-form-item>
         <el-form-item label="每年出现次数" prop='years'>
          <el-input  v-model="reci_info.years"  style="width:500px;"></el-input>
        </el-form-item>
         <el-form-item label="来源" prop='sources'>
          <el-input  v-model="reci_info.sources"  style="width:500px;"></el-input>
        </el-form-item>
        <el-form-item label="相关热词" prop='otherKW'>
          <el-input  v-model="reci_info.otherKW"  style="width:500px;"></el-input>
        </el-form-item>
        
        <el-form-item >
           <el-button   @click="resetreciForm" plain>重置</el-button>
           <el-button type="primary" plain @click="addreci">确定</el-button>
        </el-form-item>      
      </el-form>
    </el-dialog>

    </div>
</template>
<script>
export default{
    created(){
          
           this.getKWInfo();
    },
    data(){
     return{
            infoKW:"",
            kw_list:[],
            pageNum:1,
            pageSize:10,
            total:0,
            addDialogVisible:false,
            reci_info:{
                KW:"",
                times:0,
               days:"",
               months:"",
               years:"",
              sources:"",
              otherKW:"",

            }
     };
    },

    methods:{
         async getKWInfo(){
             const kw_query={};
             kw_query.infoKW=this.infoKW;
            
             kw_query.pageNum=this.pageNum-1;
             kw_query.pageSize=this.pageSize;
             
            console.log(kw_query);
          
           const {data:res}= await this.$http.post("kw_query",kw_query);
           this.total=res.num;
           this.kw_list=res.kw_list;
           console.log(this.kw_list);
         },
        handleSizeChange(newSize)
         {
             this.pageSize=newSize;
             this.getKWInfo();
         },

         handleCurrentChange(newPage)
         {
             this.pageNum=newPage;
             this.getKWInfo();
         },
         
         async juti(name)
            {
                console.log(name);
               this.$router.push({
                    path: '/infoKW1',
                    query: {
                        KW:name,
                    }    
                    })
            },
            async shanchu(kw){
      this.$confirm('确定要删除吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
            
            const order_query={};
             order_query.kw=kw;
             const {data:res}= await this.$http.post("shanchureci",order_query);

             if(res=="false")
               { 
                      this.$message.warning("删除失败，请重试！");
                   this.getKWInfo();
              }else if(res=="ok")
               {
                 this.$message.success("删除成功！！！");
                 this.getKWInfo();
              }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      
      
    },
     resetreciForm(){
            this.$refs.reciFormRef.resetFields();
      },
      async addreci(){
             
          console.log(this.reci_info);
           const {data:res}= await this.$http.post("addreci",this.reci_info);
          if(res=="false")
               { 
                      this.$message.warning("添加失败，请重试！");
                this.getKWInfo();

              }else if(res=="ok")
               {
                 this.$message.success("添加成功！！！");
                 this.getKWInfo();
                 this.addDialogVisible=false;
              }
         },
   
}
}
</script>
<style lang="less" scoped>
 .demo-table-expand {
        font-size: 20;
    }
.demo-table-expand label {
        width: 150px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 20;
        width: 33%;
        font-size:40;
        font-family: "微软雅黑", Courier, monospace;
        font-weight: 500;
    }
 
</style>