import Vue from 'vue'
import VueRouter from 'vue-router'
import HelloWorld from '../components/HelloWorld.vue'
import shouye from '../components/shouye'
import Login from '../components/Login'
import Main from '../components/Main'
import Main_user from '../components/Main_user'
import welcome from '../components/Welcome'
import addguanli from '../components/addguanli'
import queryKW from '../components/queryKW'
import infoKW from '../components/infoKW'
import KWfenxi from '../components/KWfenxi'
import guanliurl from '../components/guanliurl'
import lianjie from '../components/lianjie.vue'
import sourcefenxi from '../components/sourcefenxi.vue'
import guanlilianjie from '../components/guanlilianjie.vue'
import guanliuser from '../components/guanliuser.vue'
import guanlireci from '../components/guanlireci.vue'
import monthsKW from '../components/monthsKW.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:"/shouye",
    
  },
  {
    path: '/shouye',
    component: shouye,
    redirect:'/Welcome',
    children:[
      {path:  '/Login', component: Login,},
      {path:'/Welcome',component:welcome},
      {path:'/KWfenxi2',component:KWfenxi},
    ]
  },
  {
    path: '/Main',
    component:Main,
    redirect:'/Welcome1',
    children:[
      {path:'/Login1',component:Login,},
      {path:'/Welcome1',component:welcome},
      {path:'/queryKW',component:queryKW},
      {path:'/infoKW',component:infoKW},
      {path:'/KWfenxi',component:KWfenxi},
      {path:'/lianjie',component:lianjie},
      {path:'/sourcefenxi',component:sourcefenxi},
      {path:'/monthsKW',component:monthsKW},
    ]
  },
  {
    path:'/Main_user',
    component:Main_user,
    redirect:'/Welcome2',
    children:[
      {path:'/Login2',component:Login},
      {path:'/Welcome2',component:welcome},
      {path:'/addguanli',component:addguanli},
      {path:'/guanliurl',component:guanliurl},
      {path:'/guanlilianjie',component:guanlilianjie},
      {path:'/guanliuser',component:guanliuser},
      {path:'/guanlireci',component:guanlireci},
      {path:'/infoKW1',component:infoKW},
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
